














































import is from 'is'
import Utils from '@/utils/index'
import api from '@/utils/api'
import mixin from '@/utils/mixin'
export default {
	mixins:[mixin],
	computed: {
	    userInfo() {
	        return this.$store.state.app.userInfo;
	    }
	},
	data() {
		return {
			levellist:[],
			is_show:false,
			page:1
		};
	},
	methods:{
		async init(){
			let _this = this;
			let bid = uni.getStorageSync("bid")
			Utils.loading('正在加载');
			//获取等级列表
			let data = {
				page : _this.page,
				bid:bid
			};
			let response = await api.getLevel(data);
			console.log(response)
			if(response.status == 1){
				Utils.loaded();
				_this.levellist = response.data.data
			}
		},
		deletelevel(id){
			console.log(id)
			let _this = this
			uni.showModal({
				title: '提示',
				content: '是否确认删除该等级？',
				success: function (res) {
					if (res.confirm) {
						Utils.loading('正在删除');
						_this.deleteList(id)
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		async deleteList(id){
			let response = await api.deleteLevel({id:id})
			console.log(response)
			if(response.status == 1){
				Utils.loaded();
				Utils.toast(response.message)
				this.init()
			}else{
				Utils.loaded();
				Utils.toast(response.message)
			}
		},
		viewlevel(type,id){
			if(type == 'add'){
				uni.navigateTo({
					url:'/pages/shop/leveldetail'
				})
			}else{
				uni.navigateTo({
					url:'/pages/shop/leveldetail?id='+id
				})
			}
		}
	},
	onShow(){
		let _this = this
		if(!_this.shouldLogin){
			_this.init()
		}
	}
}
